import './Welcome.css';
import React, { useContext } from 'react';
import { Link } from "react-router-dom";
import Context from '../Context';
import Header from '../Header/Header';

const Welcome = () => {
    const ctx = useContext(Context); 

    sessionStorage.removeItem('selected-day');
    sessionStorage.removeItem('selected-time');

    return (
      <React.Fragment>
        <Header/>
        <main>
        <section>
            <article className="information">
                <h2>Buscando saln?</h2>
                <p>Sabemos que muchas veces ests en la universidad y quieres o necesitas conseguir un saln libre, y puede ser muy difcil encontrarlo o siquiera saber si hay alguno. <br/><br/> Sobrecupo es una plataforma web que te permite visualizar los salones que se encuentran disponibles en un momento especfico de la semana, como por ejemplo... <b>Justo ahora!</b></p>
            </article>

            <article className="information">
                <h2>Open Source</h2>
                <p>Este es un proyecto de la comunidad Uniandina para la comunidad Uniandina. El cdigo que hace posible esta herramienta es tuyo. Anmate a proponer nuevas funcionalidades, cambios de diseo, optimizaciones, o lo que t quieras. Tu aporte impactar toda la comunidad Uniandina!</p>
            </article>

            <article className="information">
                <h2>Comienza! <span role="img" aria-label="Boom"></span></h2>
                <p>Para mantener la informacin actualizada, cada da sers redirigido a esta pgina para volver a cargar los datos. No te preocupes, es bastante rpido.</p>
            </article>

            <article className="information">
                <h2>Fcil de manejar</h2>
                <ol>
                  <li>Selecciona el da de la semana y la hora a la que deses buscar (<b style={{'fontFamily':'consolas'}}>I</b> quiere decir mircoles) o da click en <b>Ahora</b> para seleccionar la hora actual; esta se seleccionar por defecto si no has escogido una hora.</li>
                  <br/>
                  <li>Listo! Ya puedes ver la disponibilidad de cada edificio y saln; tambin puedes filtrar para ver solo los salones libres.</li>
                </ol>
            </article>

          </section>

          <section>
            <div id="loading-placeholder" className={ctx.data === undefined ? '' : 'inactive'}>
                <div className="lds-ring"><div></div><div></div><div></div><div></div></div>
                <p><em>Un momento por favor <br/> Estamos consultando Banner</em> <span role="img" aria-label="Nice"></span></p>
            </div>

            <Link to="/buildings" className="avoid-underline">
              <button id="btn-start" className={ctx.data === undefined ? 'inactive' : ''} type="button">Encuentra salones</button>
            </Link>
          </section>
        </main>
      </React.Fragment>
    )
}

export default Welcome;
